<template>
  <div class="v-leftbar">
    <Logo></Logo>
    <div class="v-left-ul-wrapper">
      <ul class="v-leftbar-ul" v-for="ul in leftbarData">
        <h3 class="tle">{{ul.name}}</h3>
        <LeftItem :type="li.icon" :route="li.route" v-for="li in ul.items" :key="li.name">
          {{li.name}}
        </LeftItem>
      </ul>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
import Logo from './logo.vue'
import LeftItem from './left-item.vue'
export default {
  components: {
    Logo, LeftItem
  },
  data () {
    return {
      leftbarData: [
        {
          name: '',
          route: '',
          items: [
            {
              name: '',
              icon: 'judge',
              route: '/start/install'
            }
          ]
        },
        {
          name: '',
          route: '',
          items: [
            {
              name: 'Grid ',
              icon: 'memo',
              route: '/components/grid'
            },
            {
              name: 'Toast ',
              icon: 'comment',
              route: '/components/toast'
            },
            {
              name: 'Alert ',
              icon: 'warning',
              route: '/components/alert'
            }
          ]
        },
        {
          name: '',
          items: [
            {
              name: 'DOM',
              icon: 'spanner',
              route: '/tools/dom'
            }
          ]
        }
      ]
    }
  }
}
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import "../styl/constant.styl"
.v-leftbar
  width: 17%
  border-right: 1px solid darkBorder
  .v-left-ul-wrapper
    position: sticky
    top: 0
    .v-leftbar-ul
      font-size: 16px
      margin: 8px 0
      cursor: default
      .tle
        height: 48px
        line-height: 48px
        font-size: 14px
        padding-left: 28px
        color: blackText
</style>



